.navbar.navbar-default {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  margin: 0;
  border-radius: 0;
  z-index: 10000;
}

.navbar-collapse.in {
  overflow: visible;
}

.navbar-toggle {
  padding: 5px 5px 3px 5px;
}

.glyphicon {
  font-size: 20px;
  width: 30px;
  color: black;
}


